export default class Caption {
  constructor(el) {
    this.el = el

    this.setVariables()
    this.addListeners()
  }

  setVariables() {
    this.caption = this.el.querySelector('.caption')
    this.toggle = this.el.querySelector('.caption__toggle')
  }

  addListeners() {
    this.caption.addEventListener('click', event => {
      event.stopPropagation()
    })
    this.toggle.addEventListener('click', this.toggleCaption.bind(this))
    window.addEventListener('click', this.closeCaption.bind(this))
  }

  toggleCaption(event) {
    event.stopPropagation()

    if (this.el.getAttribute('data-is') === 'open') {
      this.el.setAttribute('data-is', 'closed')
    } else {
      this.el.setAttribute('data-is', 'open')
    }
  }

  closeCaption(event) {
    if (this.el.getAttribute('data-is') === 'open') {
      this.el.setAttribute('data-is', 'closed')
    }
  }
}



